import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { User } from '../../../model/user.model';
import { ComentariosService } from '../../../services/comentarios.services'
import { ComentarioModel } from '../../../model/comentario.model';
import { ProyectosModel } from '../../../model/proyectos.model';
import { LikesModel } from '../../../model/likes.model';

declare var $:any;
@Component({
    moduleId: module.id,
    selector: 'widgets-cmp',
    templateUrl: 'widgets.component.html',
    providers :[ComentariosService],
})

export class WidgetsComponent implements OnInit{
    //Propiedades
    private userUID = User;
    public itemComentarios: ComentarioModel[];//Observable<any>;
    public itemProyectos: ProyectosModel[];//Observable<any>;
    public nombre_proyect: string = "";
    txtComentarioTexto :'';

    constructor(private router: Router,private router_act: ActivatedRoute, public dbService: ComentariosService){
        
        if(this.CargaInicioSesion()){

            this.router_act.queryParams.subscribe(params => {
                
                this.nombre_proyect = params['filter'];
                //carga toda la informacion de la pagina
                this.CargarProyectosComentarios();

            });
            
        }
    }

    ngOnInit(){
        $().ready(function() {             
            //Inicio - posicin inicial
            let body = $(".main-panel");
            let top = body.scrollTop() // Get position of the body
            if(top!=0){ body.animate({scrollTop:0}, '1500'); }
            //Fin - posicion inicial            
        });
    }
    //Metodos
    //Valida autenticacin
    CargaInicioSesion():boolean{
        if(localStorage.getItem("currentUser")){
            this.userUID = JSON.parse(localStorage.getItem("currentUser"));
            return true;
        }
        else{
            //Control de seguridad
            this.router.navigate(['/security/login']);
            return false;
        }
    }


    //Inicializa proyectos
    CargarProyectosComentarios(){

        let x: any;
        if(this.nombre_proyect==undefined || this.nombre_proyect==""){
            x = this.dbService.getProyectoData();
            x.snapshotChanges().subscribe(item => {
                this.itemProyectos = [];
                item.forEach(element => {
                    var y = element.payload.toJSON();
                    
                    y["$key"] = element.key;//identificador proyecto
    
    
                    let  objComentarios: ComentarioModel[];
                    let iComentario: number = 0;
                    objComentarios = [];

                    
                    //recorre la cantidad de comentarios
                    let iLengthComentario: number = 0;
                    for (var i in y["Comentarios"]) { iLengthComentario++; }
                    //fin recorrido de comentarios           
                    for (var i in y["Comentarios"]) {
                        iComentario++;
                        
                        if(iComentario >= iLengthComentario - 3){
                            objComentarios.push({
                                $key: (iComentario == iLengthComentario - 3 && iComentario >= 3 ? "..." : i),
                                Id: iComentario,
                                idUsuario : y["Comentarios"][i]["idUsuario"],
                                Descripcion : y["Comentarios"][i]["Descripcion"],
                                URLPhoto : y["Comentarios"][i]["URLPhoto"],
                                FechaCreacion: y["Comentarios"][i]["FechaCreacion"],
                            });                        
                        }
                    }                  
                    //Ordena los comentarios de ultimo a primero
                    y["Comentarios"] = objComentarios.sort((a,b)=>b.Id - a.Id);
                     
                    

                    
                    //Likes
                    let objLikes: LikesModel[];
                    objLikes = [];
                    let iLikes: any = 0;
                    for (var i in y["Likes"]) {
                        iLikes++;
                        objLikes.push({
                            $key : i,
                            IdUsuario : y["Likes"][i]["IdUsuario"],
                            FechaCreacion: y["Likes"][i]["FechaCreacion"],
                            URLPhoto : y["Likes"][i]["URLPhoto"]
                        });
                    }                   
                    y["Likes_Number"] = iLikes; //Total likes                    
                    y["Likes"] = objLikes;//likes de ultimo a primero
                    //End likes


                    this.itemProyectos.push(y as ProyectosModel);
                });
            });        
        }
        else{
            x = this.dbService.getProyectoKey(this.nombre_proyect);
            x.snapshotChanges().subscribe(item => {
                this.itemProyectos = [];
                item.forEach(element => {
                    var y = element.payload.toJSON();
                    
                    y["$key"] = element.key;//identificador proyecto


                    let  objComentarios: ComentarioModel[];
                    objComentarios = [];
                    let iComentario: number = 0;
                    for (var i in y["Comentarios"]) {
                       
                        iComentario++;                        
                        objComentarios.push({
                            $key: i,
                            Id: iComentario,
                            idUsuario : y["Comentarios"][i]["idUsuario"],
                            Descripcion : y["Comentarios"][i]["Descripcion"],
                            URLPhoto : y["Comentarios"][i]["URLPhoto"],
                            FechaCreacion: y["Comentarios"][i]["FechaCreacion"],
                        });

                    }
                    //ordena los comentarios de ultimo a primero
                    y["Comentarios"] = objComentarios.sort((a,b)=>b.Id - a.Id);

                    

                    //Likes
                    let objLikes: LikesModel[];
                    objLikes = [];
                    let iLikes: any = 0;
                    for (var i in y["Likes"]) {
                        iLikes++;
                        objLikes.push({
                            $key : i,
                            IdUsuario : y["Likes"][i]["IdUsuario"],
                            FechaCreacion: y["Likes"][i]["FechaCreacion"],
                            URLPhoto : y["Likes"][i]["URLPhoto"]
                        });
                    }                   
                    y["Likes_Number"] = iLikes; //Total likes                    
                    y["Likes"] = objLikes;//likes de ultimo a primero
                    //End likes

                    this.itemProyectos.push(y as ProyectosModel);
    
                });
            });

        }
            
    }

    insComentario(idProyecto: string){
        this.dbService.setProyecto(idProyecto);//determina a que proyecto carresponde
        this.dbService.insertComentarios(
            {
                $key: null,
                idUsuario: this.userUID.uid,
                Descripcion: this.txtComentarioTexto,
                URLPhoto: this.userUID.photoURL,
                FechaCreacion: new Date().toISOString(),
            }
        );
    }



}
