import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { User } from '../../../../model/user.model';
import { AuthData } from '../../../../providers/auth-data';

//validaciones y conexion
import * as firebase from 'firebase';
import { EmailValidator } from '../../../../validators/email';
import { UsuarioService } from '../../../../services/usuario.services'


declare var $:any;
declare var demo:any;
declare var mdp:any;
@Component({
  selector: 'app-new',
  templateUrl: './new.page.html',
  styleUrls: ['./new.page.scss'],
  providers :[UsuarioService],
})
export class NewPage implements OnInit {
  constructor(private router: Router, private dbUsuario: UsuarioService) { }

  txtEmail:'';
  txtPassword:'';
  uiCheck :'';
  txtConfirmarPassword :'';

  ngOnInit() {
    $(".plt-desktop").removeClass('nav-open');
    $(".plt-mobile").removeClass('nav-open');  
    $('.main-panel').addClass('main-panel-security');
    $('.main-panel-security').removeClass('main-panel');

    $.getScript('https://julianromerosalazar.github.io/Comunidapp/assets/js/demo.js');
    $.getScript('https://julianromerosalazar.github.io/Comunidapp/assets/js/init/initMenu.js');


    $().ready(function() {      

      //Efectomivimiento en imagen
      demo.checkFullPageBackgroundImage();

      setTimeout(function() {
          // after 1000 ms we add the class animated to the login/register card
          $('.card').removeClass('card-hidden');
      }, 700)
      
      $('ion-app > div.wrapper').perfectScrollbar();
      
      $('ion-app > div.wrapper').perfectScrollbar();
      
      //Actualiza control del menu
      try{ if(mdp){ mdp.initSidebarsCheck(); }}catch(e){ }
    });
  }

  getLoginUser(){
    return "https://julianromerosalazar.github.io/Comunidapp/#/security/login";
  }
  getLockUser(){
      return "https://julianromerosalazar.github.io/Comunidapp/#/security/lock";
  } 

  getValidarUsers(){
    if(this.txtPassword == this.txtConfirmarPassword){
      if(EmailValidator.isValid(this.txtEmail)){

        //Ejecuta metodo evento promesa
        firebase.auth().createUserWithEmailAndPassword(this.txtEmail, this.txtPassword).then(authData => {

          //lineas para eliminar sesion del usuario
          localStorage.clear();

          //Crea datos del usuario
          //registra la informacin del nuevo usuario;  
          this.dbUsuario.insertUsuario(authData.user.uid, {
            Email : authData.user.email,
            IdUniversidad : '',
            Nombres : "Usuario",
            QuienSoy : '',
            Telefono : '',
            URLPhoto : "https://julianromerosalazar.github.io/Comunidapp/assets/img/default-avatar.png",
            Estado : '1',
            Perfil : '0',    
            FechaUltimoIngreso : new Date().toISOString(),    
            FechaCreacion : new Date().toISOString()
          });      

          //redireccionamiento
          demo.Alerta("Creacin usuario", "Usuario creado correctamente, ingresa tus datos para continuar.", "success");
          this.router.navigate(['/security/login']);

        }, error => {
          demo.Alerta("Creacin usuario", "datos invalidos, verifique e intente nuevamente.", "warning");
          localStorage.clear();
        }); 
     
        

      }
      else{
        demo.Alerta("Creacin usuario", "Correo invalido, verifique e intente nuevamente.", "warning");
      }
    }
    else{
      demo.Alerta("Creacin usuario", "Confirmacin de password invarido verifique e intente nuevamente.", "warning");
    }
  }

}
