import { Component, OnInit } from '@angular/core';
import { ROUTES } from '../.././sidebar/sidebar-routes.config';
import { MenuType } from '../.././sidebar/sidebar.metadata';
import { Router, ActivatedRoute } from '@angular/router';
import {Location, LocationStrategy, PathLocationStrategy} from '@angular/common';
import { AuthData } from '../../../providers/auth-data';

declare var $:any;
@Component({
    moduleId: module.id,
    selector: 'navbar-cmp',
    templateUrl: 'navbar.component.html',
    providers: [ AuthData ]
})

export class NavbarComponent implements OnInit{
    private listTitles: any[];
    public txtBuscar: string = '';
    location: Location;
    constructor(private router: Router, private router_act: ActivatedRoute, private objSalir: AuthData, location:Location) {
        this.location = location;
    }

    ngOnInit(){       
        this.listTitles = ROUTES.filter(listTitle => listTitle.menuType !== MenuType.BRAND);
        
        //Carga descripcion filtro
        this.router_act.queryParams.subscribe(params => {
            this.txtBuscar =  (params['filter'] ? (params['filter']).toLowerCase().replace() : "");
        });
    }
    
    getTitle(){
        var titlee = this.location.prepareExternalUrl(this.location.path());
        if(titlee.charAt(0) === '#'){
            titlee = titlee.slice( 2 );
        }
        if(titlee.split('?').length>0){
            titlee = titlee.split('?')[0];
        }
        for(var item = 0; item < this.listTitles.length; item++){
            if(this.listTitles[item].path === titlee){
                return this.listTitles[item].title;
            }
        }
        return 'Comunidapp';
    }
    public isBusqueda():boolean{
        if(this.location.prepareExternalUrl(this.location.path()).toLowerCase().substring(0,9) == '#/widgets'){
            return true;
        }
        else {
            return false;
        }
    }

    getPath(){
        return this.location.prepareExternalUrl(this.location.path());
    }
    getSalir(){
        localStorage.clear();
        this.objSalir.logoutUser().then(()=>{
            this.router.navigate(['/security/login']);
        });
    }

    getBuscar(){
        if(this.txtBuscar != ""){
            this.router.navigate(['/widgets'], {queryParams: {filter: this.txtBuscar }})
        }
        else{
            this.router.navigate(['/widgets'])
        } 
    }
}
