import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { User } from '../../../../model/user.model';
import { UsuarioService } from '../../../../services/usuario.services'
import { UsuarioModel } from '../../../../model/usuario.model';
import { FileUploadModel } from '../../../../model/fileupload.model';

declare var $:any;
declare var demo:any;
@Component({
    moduleId: module.id,
    selector: 'user-cmp',
    templateUrl: 'user.component.html',
    providers :[UsuarioService],
})

export class UserComponent implements OnInit{
    private objUser = User;
    public objUsuario: UsuarioModel;

    progress: { percentage: number } = { percentage: 0 };
    selectedFiles: FileList;
    currentFileUpload: FileUploadModel;

    constructor(private router: Router, private dbUsuario: UsuarioService){
        if(this.CargaInicioSesion()){

            //verifica y carga la existencia del usuario
            this.CargarUsuarios();

        }
    }

    //Metodos
    //Valida autenticacin
    CargaInicioSesion():boolean{
        if(localStorage.getItem("currentUser")){
            this.objUser = JSON.parse(localStorage.getItem("currentUser"));
            return true;
        }
        else{
            //Control de seguridad
            this.objUser = User;
            this.router.navigate(['/security/login']);
            return false;
        }
    }
    
    private CreaNuevoUsuario(){

        //registra la informacin del nuevo usuario
        this.dbUsuario.insertUsuario(this.objUser.uid, {
            Email : this.objUser.email,
            IdUniversidad : '',
            Nombres : this.objUser.displayName,
            QuienSoy : '',
            Telefono : '',
            URLPhoto : (this.objUser.photoURL!="" ? this.objUser.photoURL : "https://julianromerosalazar.github.io/Comunidapp/assets/img/default-avatar.png"),
            Estado : '1',
            Perfil : '0',    
            FechaUltimoIngreso : new Date().toISOString(),    
            FechaCreacion : new Date().toISOString()
        });

        //carga nuevamente el usuario
        this.CargarUsuarios()
    }

    CambioEstado(value:string){
        this.objUsuario.Estado = value;
    }

    //Inicializa proyectos
    CargarUsuarios(){
        var x = this.dbUsuario.getDataKey(this.objUser.uid);
        x.snapshotChanges().subscribe(item => {

            item.forEach(element => {
                var y = element.payload.toJSON();
                
                y["$key"] = element.key;//identificador proyecto
                this.objUsuario = y as UsuarioModel;

            });

            if(!item.length){
                this.CreaNuevoUsuario();
                return;
            }
        });
    }

    selectFile(event) {
        const file = event.target.files.item(0)
 
        if (file.type.match('image.*')) {
          this.selectedFiles = event.target.files;
        } else {
          alert('invalid format!');
        }
    }

    upload(keyUser:string,keyImage:string) {

        //valdida la existencia de una imagen
        if(this.selectedFiles != undefined){

            const file = this.selectedFiles.item(0)
            this.selectedFiles = undefined
         
            this.currentFileUpload = new FileUploadModel(file);
            this.dbUsuario.pushFileToStorage(keyUser, keyImage, this.currentFileUpload, this.progress);
        }

    }    

    ModificarUsuario(){
        
        //Actualiza imagen
        this.upload(this.objUser.uid,this.objUsuario.$key);

        //Tabla local
        this.dbUsuario.updateUsuario(this.objUser.uid, this.objUsuario);
        demo.Alerta("Perfil usuario", "Se ha actualizada correctamente", "success");

    }

    ngOnInit(){
        
        $().ready(function() {

            //Inicio - posicin inicial
            let body = $(".main-panel");
            let top = body.scrollTop() // Get position of the body
            if(top!=0){ body.animate({scrollTop:0}, '1500'); }
            //Fin - posicion inicial

        });
        
    }
}
