import { Injectable } from '@angular/core';
import { AngularFireDatabase, AngularFireList, AngularFireObject } from '@angular/fire/database';
import { AngularFirestore } from '@angular/fire/firestore';
import { UsuarioModel } from '../model/usuario.model';
import { FileUploadModel } from '../model/fileupload.model';
import * as firebase from 'firebase';

@Injectable()
export class UsuarioService {
  public itemRef: AngularFireList<any>;

  fileUploads: AngularFireList<FileUploadModel[]>;
  constructor(private db :AngularFireDatabase) {}

  getData(){
    this.itemRef = this.db.list(`Usuarios`);
    return this.itemRef;
  }

  getDataKey(key: string){
    this.itemRef = this.db.list(`Usuarios/${key}`);
    return this.itemRef;
  }

  insertUsuario(key: string, usuario : UsuarioModel): string
  {
    const itemPath =  `Usuarios/${key}`;
    return this.db.list(itemPath).push(usuario).key;
  }

  updateUsuario(key: string,usuario : UsuarioModel) {
    const itemPath =  `Usuarios/${key}/${usuario.$key}`;
    
    firebase.database().ref(itemPath).update({
      Email: usuario.Email,
      IdUniversidad : usuario.IdUniversidad,
      Nombres: usuario.Nombres,
      QuienSoy : usuario.QuienSoy,
      Telefono: usuario.Telefono,
      URLPhoto: usuario.URLPhoto,
      FechaModificacion:  new Date().toISOString(),
      Estado : usuario.Estado
    });
  }

  pushFileToStorage(key: string, keyImagen: string, fileUpload: FileUploadModel, progress: {percentage: number}) {
    const storageRef = firebase.storage().ref();
    const uploadTask = storageRef.child(`/Usuarios/${key}.${fileUpload.file.name.split('.')[1]}`).put(fileUpload.file);

    uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED,
      (snapshot) => {
        // in progress
        const snap = snapshot as firebase.storage.UploadTaskSnapshot
        progress.percentage = Math.round((snap.bytesTransferred / snap.totalBytes) * 100)
      },
      (error) => {
        // fail
        console.log(error)
      },
      () => {
        // success
        uploadTask.snapshot.ref.getDownloadURL().then(function(downloadURL) {
          //actualiza la url de la imagen del proyecto
          firebase.database().ref(`Usuarios/${key}/${keyImagen}`).update({
            URLPhoto: `${downloadURL}`
          });
        });
      }
    );
    
  }

 
  getFileUploads(query = {}) {
    this.fileUploads = this.db.list('/Usuarios');
    return this.fileUploads
  }
 
  deleteFileUpload(fileUpload: FileUploadModel) {
    this.deleteFileDatabase(fileUpload.$key)
      .then(() => {
        this.deleteFileStorage(fileUpload.name)
      })
      .catch(error => console.log(error))
  }
 
  private deleteFileDatabase(key: string) {
    return this.db.list(`/Usuarios/`).remove(key)
  }
 
  private deleteFileStorage(name: string) {
    const storageRef = firebase.storage().ref()
    storageRef.child(`/Usuarios/${name}`).delete()
  }

}