import { Injectable } from '@angular/core';
import { AngularFireDatabase, AngularFireList, AngularFireObject } from '@angular/fire/database';
import { LikesModel } from '../model/likes.model';

@Injectable()
export class LikesService {
  likesList: AngularFireList<any>;
  keyProyecto: string = "1";
  
  constructor(private firebase :AngularFireDatabase){ }

  setProyecto(idProyecto: string): void{
    this.keyProyecto = idProyecto;
  }

  getProyectoData(): AngularFireList<any>{
    return this.firebase.list(`Proyecto`);
  }

  getData(){
    this.likesList = this.firebase.list(`Proyecto/${this.keyProyecto}/Likes`);
    return this.likesList;
  }

  // Return a single observable item
  getItem(idLikes: string) {
    const itemPath =  `Proyecto/${this.keyProyecto}/Likes/${idLikes}`;
    return this.firebase.list(itemPath);
  } 

  insertLikes(likes : LikesModel)
  {
    const itemPath =  `Proyecto/${this.keyProyecto}/Likes`;
    this.firebase.list(itemPath).push({
      IdUsuario: likes.IdUsuario,
      FechaCreacion: likes.FechaCreacion,
      URLPhoto: likes.URLPhoto
    });
  }

  updateComentarios(likes : LikesModel){
    this.likesList.update(likes.$key,
      {
        IdUsuario: likes.IdUsuario,
        FechaCreacion: likes.FechaCreacion
      });
  }

  deleteLikes($key : string){
    this.firebase.list(`Proyecto/${this.keyProyecto}/Likes`).remove($key);
  }

}