import { Component, OnInit } from '@angular/core';
import { ROUTES } from './sidebar-routes.config';
import { MenuType } from './sidebar.metadata';
import { User } from '../../model/user.model';
import { UsuarioService } from '../../services/usuario.services'

declare var $:any;

@Component({
    moduleId: module.id,
    selector: 'sidebar-cmp',
    templateUrl: 'sidebar.component.html',
    providers :[UsuarioService],
})

export class SidebarComponent implements OnInit {
    constructor(private dbUsuario: UsuarioService){
        this.objUser.displayName = "";
        if(localStorage.getItem("currentUser")){
            let SessionUser = User;
            SessionUser = JSON.parse(localStorage.getItem("currentUser"));
            var x = this.dbUsuario.getDataKey(SessionUser.uid);
            x.snapshotChanges().subscribe(item => {
    
              //lineas para almacenamiento - informacion del usuario
              item.forEach(element => {
    
                  var y = element.payload.toJSON();              
                  this.objUser.displayName = y["Nombres"];
                  this.objUser.photoURL = y["URLPhoto"];
    
              });
    

              if(this.objUser.displayName==""){
                this.objUser.displayName = "Usuario";
              }

              localStorage.setItem("currentUser", JSON.stringify(this.objUser));
              
              return;
              
            });

            
        }
    }

    public menuItems: any[];
    public objUser = User;    

    ngOnInit() {
        $.getScript('https://julianromerosalazar.github.io/Comunidapp/assets/js/sidebar-moving-tab.js');
        $(".main-panel").animate({ scrollTop: 0 }, "slow");
        this.menuItems = ROUTES.filter(menuItem => menuItem.menuType !== MenuType.BRAND);
    }

}
