import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

//validaciones y conexion
import * as firebase from 'firebase';
import { EmailValidator } from '../../../../validators/email';
import { User } from '../../../../model/user.model';

declare var $:any;
declare var demo:any;
declare var mdp:any; // ejecuta script en la aplicacion del menu
@Component({
  moduleId: module.id,
  selector: 'app-login',
  templateUrl: './login.page.html',
  styleUrls: ['./login.page.scss'],
})
export class LoginPage implements OnInit {
  constructor(private router: Router) {
    
   }
  
  showNav = true;
  txtEmail: '';
  txtPassword: '';

  ngOnInit() {
    $(".plt-desktop").removeClass('nav-open');
    $(".plt-mobile").removeClass('nav-open');  
    $('.main-panel').addClass('main-panel-security');
    $('.main-panel-security').removeClass('main-panel');

    $.getScript('https://julianromerosalazar.github.io/Comunidapp/assets/js/demo.js');
    $.getScript('https://julianromerosalazar.github.io/Comunidapp/assets/js/init/initMenu.js');

    //material-dashboard-angular.js
    $().ready(function() {
      
      //Actualiza control del menu
      try{ if(mdp){ mdp.initSidebarsCheck(); }}catch(e){ }

      //Efectomivimiento en imagen
      demo.checkFullPageBackgroundImage();
      setTimeout(function() {
        // after 1000 ms we add the class animated to the login/register card
        $('.card').removeClass('card-hidden');
      }, 700);
      //Inicio - posicin inicial
      let body = $(".main-panel");
      let top = body.scrollTop() // Get position of the body
      if(top!=0){ body.animate({scrollTop:0}, '1500'); }
      //Fin - posicion inicial

      $('ion-app > div.wrapper').perfectScrollbar();

    });
  }

  getAuthenticaGoogle(){
    return firebase.auth().signInWithRedirect(new firebase.auth.GoogleAuthProvider())
      .then(() => {
          $('.main-panel-security').addClass('main-panel');
          return firebase.auth().getRedirectResult;
    });
  }

  getAuthenticaFacebook(){
    return firebase.auth().signInWithRedirect(new firebase.auth.FacebookAuthProvider())
    .then(result => {
        $('.main-panel-security').addClass('main-panel');
        return firebase.auth().getRedirectResult;
    });
  }


  getValidarUsers(){
    if(EmailValidator.isValid(this.txtEmail)){

      firebase.auth().signInWithEmailAndPassword(this.txtEmail, this.txtPassword).then(authData => {

        //lineas para almacenamiento - informacion del usuario
        User.displayName = (authData.user.displayName? authData.user.displayName : ""),
        User.email = (authData.user.email ? authData.user.email : ""),
        User.photoURL = (authData.user.photoURL ? authData.user.photoURL : ""),
        User.uid = (authData.user.uid ? authData.user.uid : ""),
        User.providerData = (authData.user.providerData ? authData.user.providerData : []),
        localStorage.setItem("currentUser", JSON.stringify(User));
        
        $('.main-panel-security').addClass('main-panel');   
        
        //inicia control
        $.getScript('https://julianromerosalazar.github.io/Comunidapp/assets/js/init/initMenu.js');        

        //redireccionamiento
        this.router.navigate(['/dashboard']);

      }, error => {
        demo.Alerta("Inicio sesin", "Usuario y/o clave invalida.", "warning");
        console.log("Error loggueo fallido");
        if(localStorage.getItem("currentUser")){
          localStorage.removeItem("currentUser");
        }
      });      
    }
    else{
      demo.Alerta("Inicio sesin", "Correo invalido, verifique e intente nuevamente.", "warning");
    }
  }

  getNewUser(){
      return "https://julianromerosalazar.github.io/Comunidapp/#/security/new";
  }
  getLockUser(){
      return "https://julianromerosalazar.github.io/Comunidapp/#/security/lock";
  }    

}
