import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { UsuarioService } from '../../../../services/usuario.services'
import { UsuarioModel } from '../../../../model/usuario.model';

declare var $:any;
@Component({
    moduleId: module.id,
    selector: 'userline-cmp',
    templateUrl: 'userline.component.html',
    providers :[UsuarioService],
})

export class UserLineComponent implements OnInit{
    public objUsuario: UsuarioModel;

    constructor(private router: Router, private router_act: ActivatedRoute, private dbUsuario: UsuarioService){
        if(this.CargaInicioSesion()){

            this.router_act.queryParams.subscribe(params => {
                
                let strkeyUser = params['filter'];                
                this.CargarUsuarios(strkeyUser);//carga toda la informacion de la pagina

            });

        }
    }

    //Metodos
    //Valida autenticacin
    CargaInicioSesion():boolean{
        if(localStorage.getItem("currentUser")){
            return true;
        }
        else{
            //Control de seguridad
            this.router.navigate(['/security/login']);
            return false;
        }
    }
    
    CambioEstado(value:string){
        this.objUsuario.Estado = value;
    }

    //Inicializa proyectos
    CargarUsuarios(keyUser: string){
        var x = this.dbUsuario.getDataKey(keyUser);
        x.snapshotChanges().subscribe(item => {

            item.forEach(element => {
                
                var y = element.payload.toJSON();                
                y["$key"] = element.key;//identificador proyecto
                this.objUsuario = y as UsuarioModel;

            });

            if(!item.length){
                return;
            }
        });
    }


    ngOnInit(){

        $().ready(function() {

            //Inicio - posicin inicial
            let body = $(".main-panel");
            let top = body.scrollTop() // Get position of the body
            if(top!=0){ body.animate({scrollTop:0}, '1500'); }
            //Fin - posicion inicial

        });

    }
}
